<template>
    <mt-swipe :auto="time">
        <mt-swipe-item v-for="(item, index) in list" :key="index">
            <a href="#">
                <img :src="item.imgUrl" class="swipe-item-img" />
            </a>
        </mt-swipe-item>
    </mt-swipe>
</template>
<script>
    export default {
        props:{
            list:Array,
            time:{
                type:Number,
                default:() => {
                    return 3000
                }

            }
        }
    }
</script>
<style>
    .page-swipe .mint-swipe {
        height: 200px;
        min-height: 200px;
        color: #fff;
        font-size: 30px;
        text-align: center;
    }
    .page-swipe .mint-swipe-item {
        line-height: 200px;
    }
    
    .page-swipe .mint-swipe-item .swipe-item-img{
        width: 100%;
        display: block;
    }
</style>




